@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: "Noto Sans JP", sans-serif;
}

body {
  margin: 0;
  background-image: url(/static/media/Gradient-Mesh-2.cb4ab9ce.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

.expense-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
  margin: 0 auto;
  width: 100%;
  margin-top: 0.5rem;
  transition: 0.2s ease;
  background: rgba(255, 255, 255, 0.25);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.18);
  color: #2a2a2a;
  cursor: pointer;
}

.expense-item:hover {
  transition: 0.5s ease;
  background: rgba(255, 255, 255, 0.8);
}

.expense-item__description {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-flow: column-reverse;
  justify-content: space-between;
  flex: 1 1;
  cursor: pointer;
}

.expense-item h2 {
  color: #2a2a2a;
  font-size: 1rem;
  flex: 1 1;
  margin: 0 1rem;
  cursor: pointer;
  word-wrap: break-word;
}

.expense-item__price {
  margin-left: auto;
  font-size: 1rem;
  font-weight: bold;
  color: white;
  background: rgb(248, 72, 94);
  padding: 0.5rem;
  border-radius: 12px;
  cursor: pointer;
}

.expense-item__descbox {
  display: flex;
  flex-direction: column;
}

.expense-item__descbox span {
  margin: 0 1rem;
  font-style: italic;
}

@media (min-width: 580px) {
  .expense-item__description {
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    flex: 1 1;
  }

  .expense-item__description h2 {
    font-size: 1.25rem;
  }

  .expense-item__price {
    font-size: 1.25rem;
    padding: 0.5rem 1.5rem;
  }
}

@media only screen and (max-width: 768px) {
  .expense-item__description {
    width: 100%;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    flex: 1 1;
    cursor: default;
  }

  .expense-item h2 {
    margin: 0 0.5rem;
  }

  .expense-item__descbox span {
    margin: 0 0.5rem;
  }
}

.expense-date {
  display: flex;
  flex-direction: column;
  width: 5.5rem;
  height: 5.5rem;
  background: rgba(255, 255, 255, 0.5);
  -webkit-backdrop-filter: blur(20px);
          backdrop-filter: blur(20px);
  color: #2a2a2a;
  border-radius: 12px;
  align-items: center;
  justify-content: center;
}

.expense-date__month {
  font-size: 0.75rem;
  font-weight: bold;
  margin-bottom: 5px;
}

.expense-date__year {
  font-size: 0.75rem;
}

.expense-date__day {
  font-size: 1.5rem;
  font-weight: bold;
}

@media only screen and (max-width: 768px) {
  .expense-date {
    width: 4rem;
    height: 4rem;
  }
  .expense-date__month {
    font-size: 0.7rem;
    font-weight: bold;
  }

  .expense-date__year {
    font-size: 0.7rem;
  }

  .expense-date__day {
    font-size: 1rem;
    font-weight: bold;
  }
}

.card {
  border-radius: 12px;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
}

.expenses-filter {
  color: black;
}

.expenses-filter__control {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
}

.expenses-filter label {
  /* font-weight: bold; */
  margin-bottom: 0.5rem;
}

.expenses-filter select {
  width: 100%;
  font: inherit;
  font-size: 1.5rem;
  font-weight: bold;
  border-radius: 6px;
  background-color: rgba(31, 31, 31, 0);
  color: black;
  border: 0;
  outline: none;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

@media only screen and (max-width: 768px) {
  .expenses-filter select {
    font-size: 1.5rem;
  }
}

.chart-bar {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.chart-bar__inner {
  height: 100%;
  width: 17px;
  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 12px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.chart-bar__fill {
  background-color: #f8485e;
  width: 100%;
  transition: all 0.3s ease-out;
}

.chart-bar__label {
  font-size: 0.7rem;
  text-align: center;
}

.chart {
  padding: 1rem;
  text-align: center;
  display: flex;
  justify-content: space-around;
  height: 8rem;
}

.expenses {
  /* padding: 1rem;
  background-color: rgb(31, 31, 31);
  margin: 2rem auto;
  width: 50%;
  max-width: 95%;
  border-radius: 12px;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25); */
  padding: 1rem;
  margin: 0.5rem auto;
  width: 50%;
  max-width: 95%;
  background: rgba(255, 255, 255, 0.2);
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
  backdrop-filter: blur(11.5px);
  -webkit-backdrop-filter: blur(11.5px);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.18);
}

.no-expenses-found__msg {
  text-align: center;
}

.no-expenses-found__container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

img {
  width: 25%;
}

@media only screen and (max-width: 768px) {
  .expenses {
    width: 90%;
    padding: 0.5rem;
    margin: 1rem auto;
  }
}

.overlay {
  background-color: rgba(0, 0, 0, 0.6);
  width: 100vw;
  height: 100vh;
  top: 0;
  position: fixed;
  z-index: 15;
}

.expense-edit__container {
  position: fixed;
  padding: 2rem 3rem;
  left: 35vw;
  top: 10vh;
  z-index: 20;

  background: rgba(255, 255, 255, 0.5);
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
  backdrop-filter: blur(11.5px);
  -webkit-backdrop-filter: blur(11.5px);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.18);
  flex-direction: column;
}

.expense-edit__container h2 {
  margin: 0 0 1rem 0;
}

.expense-edit__container label {
  margin-top: 0.5rem;
}

.expense-edit__current-info {
  background: rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.18);
  color: #2a2a2a;
}

.expense-edit__expense-name {
  padding: 1rem;
}

.button-container {
  margin-top: 1rem;
  display: flex;
  justify-content: space-around;
}

.cancel {
  padding: 1.2rem 1.5rem;
  margin-top: 1rem;
  background-color: white;
  color: black;
  border-radius: 12px;
  border: none;
  font-size: 1rem;
  cursor: pointer;
}

.cancel:hover {
  background-color: rgba(230, 230, 230);
}

.save {
  padding: 1.2rem 1.5rem;
  margin-top: 1rem;
  background-color: rgb(52, 6, 80);
  color: white;
  border-radius: 12px;
  border: none;
  font-size: 1rem;
  cursor: pointer;
}

.save:hover {
  background-color: rgb(81, 6, 116);
}

.delete {
  padding: 1.2rem 1.5rem;

  background-color: rgb(248, 72, 94);
  color: white;
  border-radius: 12px;
  border: none;
  font-size: 1rem;
  cursor: pointer;
  width: 100%;
}

.delete:hover {
  background-color: rgb(200, 1, 60);
}

.expense-edit__container p {
  font-size: 1rem;
}

@media only screen and (max-width: 415px) {
  .expense-edit__container {
    font-size: 0.8rem;
    padding: 1rem;
    left: 3%;
    top: 3%;
    margin: 0 auto;
    z-index: 20;
  }
}

.new-expense__controls {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 1rem;
  gap: 1rem;
  margin-bottom: 1rem;
  text-align: left;
}

.new-expense__control label {
  font-weight: bold;
  margin-bottom: 0.5rem;
  display: block;
}

.new-expense__control__select {
  font: inherit;
  font-size: 1.1rem;
  padding: 12px 15px 12px 24px;
  border-radius: 2rem;
  outline: none;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  border: 1px solid #ccc;
  width: 20rem;
  max-width: 100%;
  transition: 0.2s ease;
}

.new-expense__control__select:focus {
  border: 1px solid #41444b;
  transition: 0.2s ease;
}

.new-expense__control__input {
  font: inherit;
  padding: 12px 15px 12px 24px;
  border-radius: 2rem;
  border: 1px solid #ccc;
  width: 20rem;
  max-width: 100%;
  outline: none;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  transition: 0.2s ease;
  text-align: center;
}

.new-expense__control__input:focus {
  border: 1px solid #41444b;
  transition: 0.2s ease;
}

.new-expense__control__error {
  font: inherit;
  padding: 12px 15px 12px 24px;
  border-radius: 2rem;
  border: 1px solid #ff4848;
  width: 20rem;
  max-width: 100%;
  outline: none;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  text-align: center;
}

.new-expense__actions {
  display: flex;
  justify-content: space-between;
}

.new-expense__actions button {
  padding: 1rem !important;
}

.new-expense__control p {
  margin: 0 auto;
  color: #ff4848;
  font-style: italic;
  text-align: center;
}

@media only screen and (max-width: 768px) {
  .new-expense__control input {
    font-size: 1.1rem;
    padding: 8px 10px 8px 12px;
    width: 19rem;
  }

  .new-expense__control select {
    font: inherit;
    padding: 8px 10px 8px 12px;
    width: 19rem;
    font-size: 1.1rem;
    text-align: center;
  }
}

.new-expense {
  padding: 1rem;
  margin: 0.5rem auto;
  width: 50%;
  max-width: 95%;
  text-align: center;
  background: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(11.5px);
  -webkit-backdrop-filter: blur(11.5px);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.18);
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
}

.new-expense button {
  font: inherit;
  cursor: pointer;
  padding: 1rem 2rem;
  border: 1px solid #40005d;
  background-color: #40005d;
  color: white;
  border-radius: 12px;
  margin-right: 1rem;
}

.new-expense button:hover,
.new-expense button:active {
  background-color: #510674;
  border-color: #510674;
}

.new-expense button.alternative {
  color: #220131;
  border-color: transparent;
  background-color: transparent;
}

.new-expense button.alternative:hover,
.new-expense button.alternative:active {
  background-color: #ddb3f8;
}

@media only screen and (max-width: 768px) {
  .new-expense {
    width: 90%;
    margin: 1rem auto;
  }
}

